
div.misc-user{
	float:left;
	text-align:left;
	display:block;
	right:5px;
}
div.misc-user a{
  	padding:0px 5px;
	color:blue;
	background: transparent;
	text-decoration: none;
}
div.misc-user a:link {
	color:blue;
}

div.misc-user a:active {
	color:blue;
}

div.misc-user a:hover,
div.misc-user a:focus
{
	color:red;
}

div.misc-user a:visited{
	color:blue;
}

input[type="text"], input[type="password"]{
    min-width: 100px;
}

