/*
 * main.css
 * */

fieldset {
	border: 1px solid #888;
	margin-bottom: 1em;
}

legend {
	font-weight: bold;
	font-size:2em;
}

label {
	display: block;
	width: 200px;
	/*float: left;*/
	cursor: pointer;
}

.field {
	clear: both;
	margin-bottom: 2px;
}

.infos {
	color: #666;
	margin-bottom: 5px;
}

.valid-error {
	color: red;
}

.hasError {
	background: pink;
}

.has-error {
	background: pink;
}
