/*
 * layout.css
 */

/* margin top for page-header bar */
body {
	margin: 55px 20px;
}

#page-header {
	height: 45px;
	width: 100%;
	position: fixed;
	left: 0px;
	top: 0px;
	z-index: 99;
	opacity: 1;
}

#logo {
	height: 45px;
	width: 200px;
	float: left;
	margin: 0px;
	padding: 0px;
	padding-left:5px;
	display: inline-block;
}

#logo a {
	margin: 0px;
	padding: 0px;
	display: inline-block;
	height: 45px;
	line-height: 40px;
	width: 200px;
	vertical-align: top;
	background: transparent;
}

#main {
	margin: 2px 20px;
	z-index: 0;
}

#main-menu,#main-nav,#menu-orgs {
	width: 200px;
	float: left;
	height: auto;
	border: 1px solid gray;
	background: #f1f1f1;
	padding: 2px;
}

#main-menu ul li a,
#main-nav ul li a,
#menu-orgs ul li a{
	display:block;
	padding:2px;
}

#main-menu ul li a:hover,
#main-nav ul li a:hover,
#menu-orgs ul li a:hover{
	background:#99FF99;
}

#main-content{
	margin-left: 210px;
	z-index: 1;
	padding: 2px;
}

#menu-years li,
#menu-org-flags li {
	float: left;
	list-style: none;
	vertical-align: middle;
}

