/**
 * mbt.css
 * for mabetle style
 */
 
.mbt-lines {

}

.mbt-lines p {
	margin: 10px 3px;
	padding: 10px 3px;
}

div.mbt-error {
	border: 1px solid red;
	padding: 0px;
	margin: 2px;
}

div.mbt-error div {
	padding: 2px;
	display: block;
	margin: 0px;
	background-color: red;
	font-weight: bold;
}

.mbt-error {
	border: 1px solid red;
	padding: 0px;
	margin: 2px;
	display: block;
	margin: 0px;
	background-color: pink;
	font-weight: bold;
}

div.mbt-contain {
	border: 1px solid #1569C7;
	padding: 0px;
	margin: 2px 2px;
}

div.mbt-contain-head {
	padding: 2px;
	margin: 0px;
	display: block;
	font-weight: bold;
	font-size: 1.2em;
	background-color: #1569C7;
	color: white;
}


div.mbt-contain-content {
	padding: 2px;
	margin: 0px;
	background-color: #dcdcdc;
}

table.mbt-table {
	border-collapse: collapse;
	margin: 0px;
	padding: 2px;
}

table.mbt-table th {
	border: 1px solid silver;
	background-color: #1569C7;
	color: white;
}

table.mbt-table td {
	border: 1px solid silver;
	margin: 0px;
	padding: 2px;
}

div.mbt-grid {
	border-collapse: collapse;
	border-top: 1px solid silver;
	border-left: 1px solid silver;
}

table.mbt-grid thead tr{
	color: white;
	background-color: #1569C7;
}

table.mbt-grid thead tr th,
table.mbt-grid tr th {
	text-align: center;
	padding: 3px;
	white-space: nowrap;
	border-right: 1px solid silver;
	border-bottom: 1px solid silver;
	color: white;
	background-color: #1569C7;
}

table.mbt-grid tbody tr td,
table.mbt-grid tr td{
	border-right: 1px solid silver;
	border-bottom: 1px solid silver;
	padding: 2px;
}

/* message  */
div.mbt-msg {
	border: 1px solid green;
	padding: 0px;
	margin: 2px;
}

div.mbt-msg div {
	padding: 2px;
	display: block;
	margin: 0px;
	background-color: #99b14e;
	font-weight: bold;
}

div.mbt-msg ul li,div.mbt-error ul li {
	margin-left: 40px;
}

/* debug */
div.mbt-debug {
	border: 1px solid green;
	padding: 0px;
	margin: 0px;
	position: fixed;
	left: 0px;
	bottom: 0px;
	z-index: 999;
	width:100%;
	display: block;
	background: #f1f1f1;
}

div.mbt-debug div {
	padding: 2px;
	display: block;
	margin: 0px;
	background-color: #99b14e;
	font-weight: bold;
}

div.mbt-debug ul li{
	margin-left: 40px;
}

div.mbt-error {
    border: 1px solid red;
    padding: 0px;
    margin: 4px 0px;
}

div.mbt-error div.mbt-banner {
    padding: 2px;
    display: block;
    margin: 0px;
    background-color: red;
    color: white;
    font-weight: bold;
}

div.mbt-error ul {
    margin: 2px 0px;
    background-color: white;
    color: red;
}

div.mbt-error li {
    margin-left: 20px;
}

div.mbt-error-single {
    padding: 2px;
    display: block;
    margin: 0px;
    background-color: red;
    color: white;
}

html>body div.mbt-error li {
    margin-left: -20px;
}

.mbt-invisible {
    display: none;
}

label.mbt-error {
    color: red;
}

input.mbt-error, textarea.mbt-error, select.mbt-error {
    border-color: red;
    font-style: italic;
    color: red;
}

img.mbt-error-icon {
    margin-left: 4px;
    width: 16px;
    height: 16px;
    background: url(field-error-marker.gif);
}

img.mbt-autoloader-icon {
    margin-left: 4px;
    width: 16px;
    height: 16px;
    background: url(ajax-loader.gif);
}

img.mbt-sort-icon {
    margin-left: 4px;
}

div.mbt-exception-message {
    font-style: italic;
    font-size: 12pt;
    border: thin dotted silver;
    margin: 5px 0px;
    padding: 3px;
}

div.mbt-exception-report, div.mbt-env-data {
    font-family: "trebuchet ms", arial, sans-serif;
}

div.mbt-exception-report li {
    margin-left: -40px;
}

div.mbt-exception-report dt, div.mbt-env-data dt {
    color: green;
    padding-left: 2px;
    background-color: #ffffcf;
}

div.mbt-exception-report li {
    list-style: none;
}

span.mbt-exception-class-name {
    display: block;
    margin-top: 15px;
    font-size: 12pt;
    background-color: #e1e1e1;
    color: blue;
    padding: 2px 3px;
    font-weight: bold;
}

span.mbt-exception-stack-controls {
    display: block;
    float: right;
}

ul.mbt-stack-trace li {
    font-family: monaco, times, monospace;
    font-size: 10pt;
    margin-left: -25px;
    list-style: square;
}

li.mbt-usercode-frame {
    font-weight: bold;
    color: blue;
}

li.mbt-omitted-frame {
    display: none;
    color: gray;
    list-style: square;
}

h1.mbt-exception-report {
    font-family: "trebuchet ms", arial, sans-serif;
    color: red;
}

div.mbt-exception-report dt:after {
    content: ":";
}

div.mbt-exception-report dd, div.mbt-env-data dd {
    margin-left: 10px;
}

table.mbt-data-table {
    border-collapse: collapse;
    margin: 0px;
    padding: 2px;
}

table.mbt-data-table th {
    background-color: black;
    color: white;
}

table.mbt-data-table td {
    border: 1px solid silver;
    margin: 0px;
}

div.mbt-beaneditor {
    display: block;
    background: #ffc;
    border: 2px outset brown;
    padding: 2px;
    font-family: "trebuchet ms", arial, sans-serif;
}

div.mbt-beaneditor-row {
    padding: 4px 0px 2px 0px;
}

div.mbt-beaneditor-row label:after {
    content: ":";
}

dl.mbt-beandisplay {
    display: block;
    padding: 2px;
    font-family: "trebuchet ms", arial, sans-serif;
    background: #ccbe99;
    border: 2px outset black;
    width: auto;
}

dl.mbt-beandisplay dt {
    width: 250px;
    display: inline;
    float: left;
    text-align: right;
    clear: left;
    padding-right: 3px;
    vertical-align: middle;
}

dl.mbt-beandisplay dt:after {
    content: ":";
}

div.mbt-beaneditor-row label {
    width: 250px;
    display: block;
    clear: left;
    padding-right: 3px;
    vertical-align: middle;
}

div.mbt-checklist-row {
    padding: 4px 0px 2px 0px;
}

div.mbt-checklist-row label {
    text-align: right;
    padding-left: 5px;
    vertical-align: middle;
}

input.mbt-number {
    text-align: right;
}

div.mbt-beandisplay div.mbt-beandisplay-label {
    padding-right: 5px;
}

table.mbt-data-grid thead tr {
    color: white;
    background-color: #1569C7;
}

table.mbt-data-grid thead tr th {
    text-align: left;
    padding: 3px;
    white-space: nowrap;
    border-right: 1px solid silver;
    border-bottom: 1px solid silver;
    color: white;
    background-color: #1569C7;
}

table.mbt-data-grid {
    border-collapse: collapse;
    border-left: 1px solid silver;
}

table.mbt-data-grid tbody tr td {
    border-right: 1px solid silver;
    border-bottom: 1px solid silver;
    padding: 2px;
}

div.mbt-data-grid {
	font-family:Arial,'Times New Roman',Helvetica,Arial,Vernada,Tahoma,STXihei,'Microsoft YaHei',SimSun,Heiti,sans-serif;
}

div.mbt-data-grid-pager {
    margin: 8px 0px;
}

div.mbt-data-grid-pager a, div.mbt-data-grid-pager span.current {
    text-decoration: none;
    color: black;
    padding: 2px 5px;
    font-size: medium;
    border: 1px solid silver;
    margin-right: 5px;
}

div.mbt-data-grid-pager a:hover {
    border: 1px solid black;
}

div.mbt-data-grid-pager span.current {
    color: white;
    background-color: #1569C7;
}

table.mbt-data-grid tr th a {
    color: white;
}

img {
    border: none;
}

div.mbt-env-data-section {
    padding-left: 5px;
}

div.mbt-env-data dd, div.mbt-exception-report dd {
    margin-left: 25px;
    margin-bottom: 10px;
}

div.mbt-env-data li {
    margin-left: -25px;
}

div.mbt-env-data-section {
    font-size: 12pt;
    background-color: #e1e1e1;
    color: blue;
    padding: 2px 3px;
    font-weight: bold;
}

table.mbt-location-outer {
    padding: 5px;
    border-collapse: collapse;
    border: 1px solid black;
    width: 100%;
}

td.mbt-location-line {
    width: 40px;
    text-align: right;
    padding: 0px;
    background-color: #e1e1e1;
    padding-right: 3px;
    border-right: 1px solid black;
}

td.mbt-location-content {
    border-top: 1px solid silver;
    border-right: 1px solid black;
    white-space: pre;
}

td.mbt-location-current {
    background-color: #ffffcf;
}

td.mbt-location-content-first {
    border-top: 1px solid black;
}

div.mbt-palette {
    display: inline;
}

div.mbt-palette select {
    margin-bottom: 2px;
    width: 200px;
}

div.mbt-palette-title {
    color: white;
    background-color: #1569C7;
    text-align: center;
    font-weight: bold;
    margin-bottom: 3px;
    display: block;
}

div.mbt-palette-available {
    float: left;
}

div.mbt-palette-controls {
    margin: 5px 5px;
    float: left;
    text-align: center;
}

div.mbt-palette-controls button {
    display: block;
    margin-bottom: 3px;
    cursor: pointer;
}

div.mbt-palette-controls button[disabled] img {
    opacity: .25;
    cursor: default;
}

div.mbt-palette-selected {
    float: left;
    clear: right;
}

div.mbt-palette-spacer {
    clear: left;
}

img.mbt-calendar-trigger {
    padding-left: 3px;
    cursor: pointer;
}

div.mbt-autocomplete-menu {
    z-index: 9999;
}

div.mbt-autocomplete-menu ul {
    border: 2px outset #cc9933;
    background-color: #cc9933;
    padding: 4px 6px;
    overflow: auto;
}

div.mbt-autocomplete-menu li {
    color: white;
    list-style-type: none;
    padding: 0px;
    margin: 0px;
    border-bottom: 1px solid black;
    cursor: pointer;
}

div.mbt-autocomplete-menu li.selected {
    color: black;
    font-weight: bold;
}

div.mbt-error-popup span {
    background: transparent url('error-bevel-left.gif') no-repeat;
    display: block;
    line-height: 28px;
    margin-left: 0px;
    padding: 0px 5px 10px 22px;
}

html>body div.mbt-error-popup span {
    background: transparent url('error-bevel-left.png') no-repeat;
}

div.mbt-error-popup {
    background: transparent url('error-bevel-right.gif') no-repeat scroll top right;
    cursor: pointer;
    color: #fff;
    display: block;
    float: left;
    font: normal 12px arial, sans-serif;
    height: 39px;
    margin-right: 6px;
    padding-right: 29px;
    text-decoration: none;
}

html>body div.mbt-error-popup {
    background: transparent url('error-bevel-right.png') no-repeat scroll top right;
}

ul.mbt-data-list li {
    list-style-type: square;
}

div.mbt-loading {
    display: inline;
    width: auto;
    font-weight: bold;
    padding-right: 20px;
    background: transparent url(ajax-loader.gif) no-repeat right top;
}

div.mbt-dialog-overlay {
    position: fixed;
    margin: auto;
    top: 0px;
    left: 0px;
    z-index: 9990;
    width: 100%;
    height: 100%;
    background: transparent url("dialog-background.gif");
}

div.mbt-page-loading-banner {
    position: absolute;
    top: 5px;
    left: 5px;
    width: auto;
    font-weight: bold;
    padding: 2px 4px 2px 24px;
    color: black;
    background: white url(ajax-loader.gif) no-repeat 4px center;
    border-right: 2px solid black;
    border-bottom: 2px solid black;
}

span.mbt-render-object-error {
    font-weight: bold;
    font: italic;
    color: red;
}

div.mbt-available-values ul li {
    list-style-type: disc;
    margin-left: 0px;
}

div.mbt-exception-container {
    background: white;
    border: 1px solid black;
    position: fixed;
    top: 50px;
    left: 50px;
    -moz-box-shadow: 6px 6px 3px #888;
    -webkit-box-shadow: 6px 6px 3px #888;
    box-shadow: 6px 6px 3px #888;

}

div.mbt-exception-frame {
}

div.mbt-exception-container .mbt-exception-close {
    display: block;
    float: right;
    margin: 5px;
    padding-right: 2px;
    padding-left: 22px;
    height: 18px;
    cursor: pointer;
    background: #e8e8e8 url("silk/delete.png") no-repeat;
    border-radius: 5px;
}


table.mbt-data-grid thead tr th {
	text-align: center;
	white-space: pre-wrap;
}

dl.mbt-beandisplay {
	clear: left;
	margin: 10px 50px;
	padding: 20px 2px;
	display: block;
	background: #f1f1f1;
	border: 2px outset silver;
	height: auto;
	border-radius: 10px;
}

dl.mbt-beandisplay dt {
	color: green;
	white-space: nowrap;
	width: 25%;
	min-height: 22px;
	display: block;
	float: left;
	text-align: right;
	padding-right: 5px;
	vertical-align: middle;
	overflow: hidden;
}
dl.mbt-beandisplay dd {
	color: blue;
	display: block;
	min-height: 22px;
	/*margin-left: 255px;*/
}
.mbt-beandisplay-label {
	color: green;
	font-size: 1.1em;
	height: 16pt;
}
.mbt-beandisplay-value {
	color: blue;
	font-size: 11pt;
	height: 1.6em;
}
table.mbt-data-grid tr {
	/*text-align: center;*/
}
div.mbt-beaneditor {
	clear: left;
	display: block;
	background: #f1f1f1;
	border: 2px outset green;
	padding: 2px;
	border-radius: 10px;

	
}
div.mbt-error ul li {
	margin-left: 40px;
}
div.mbt-error LI {
	margin-left: 20px;
}
.mbt-exception-report,.mbt-env-data {
	clear: both;
}
.mbt-beaneditor-submit {
	margin-left: 250px;
}
div.mbt-data-grid-pager span.current {
	border-radius: 10px;

	
}
th a{
	margin:0px;
	padding:0px;
	background: transparent;
}
th a:hover{
	background: transparent;
}

table.mbt-data-grid {
	margin: 5px;
	border-spacing: 0px;
	border-collapse: collapse;
}

