#page-title {
	color: #0767c8;
}

#page-title h2 {
	color: #0767c8;
}

.apps h2,.apps h3,.seperate {
	background: #f1f1f1;
	font-size: 1em;
	color: #0767c8;
	border-bottom: 1px solid silver;
	height: 20px;
}
input:-webkit-autofill,textarea:-webkit-autofill,input,textarea {
	background-color: #99ff99;
	background-image: none;
	color: black;
}
.bg-header{
	background: #0767c8;
	background-image:linear-gradient(to bottom,#086ed5,#055db5);
}

