/* bg_img.less
 *
 */

.bg-head-img{
	background: transparent url(../images/bg_head.png) repeat-x;
}

.bg-logo {
	background: transparent url(../images/logo.png) no-repeat;
}

.page-header-nav li:hover,.page-header_user a:hover {
	display: inline-block;
	background: url('../images/app/page-header_nav_mouseover_bg.png') no-repeat;
	background-size: 55px 55px;
	background-position: center center;
}

span.nav-item-bg {
	display: inline-block;
	width: 50px;
	height: 50px;
}

span.nav-item
{
	background-position: center center;
	background-size: 50px 50px;
}

.icon-home {
	background: url('../images/icons/home_big.png') no-repeat;
}

.icon-link {
	background: url('../images/icons/apps-big.png') no-repeat;
}

.icon-logout {
	background: url('../images/icons/logout-big.png') no-repeat;
}
