/**
 * button style
 */

/* button */
.buttons {
	margin:0px 3px;
	margin-top:2px;
}

/* button or button link style */
button,
.button,
td.actions a,
.buttons a,
.mbt-beaneditor-submit,
input[type="button"],
input[type="submit"],
#submit{
	margin: 0px;
	margin-right:2px;
	padding: 1px 4px;
	color: blue;
	text-decoration: none;
	border: 1px solid #b7b7b7;
	background: linear-gradient(#fff, #ededed);
}

/* button hover or focus style */
button:focus,
button:hover,
.buttons a:hover,
td.actions a:hover,
td.actions a:focus,
.buttons a:focus,
.button:hover,
.button:focus,
input[type='button']:hover,
input[type='button']:focus,
input[type='submit']:hover,
input[type='submit']:focus,
#submit:hover,
#submit:focus,
.mbt-beaneditor-submit:hover,
.mbt-beaneditor-submit:focus{
	color:white;
	background: blue;
}

a.button,
a.button:link{
	margin: 0px;
	margin-right:2px;
	padding: 1px 4px;
	text-decoration: none;
	color: blue;
	border: 1px solid #b7b7b7;
	background: linear-gradient(#fff, #ededed);
}

a.button:visited{
	color: blue;
}

a.button:hover,
a.button:focus{
	color:white;
	background: blue;
}
