/*
 * html.css
 * default html element style
 */

* {
	margin: 0px;
	padding: 0px;
	font-size: 1em;
}

html,body {
	font-family:Arial,'Times New Roman',Helvetica,Arial,Vernada,Tahoma,STXihei,'Microsoft YaHei',SimSun,Heiti,sans-serif;
	background-color: white;
}

h1,h2,h3,h4,h5,h6 {
	text-transform: capitalize;
	font-weight: bold;
	margin: 2px 0px;
}

h1 {
	padding: 2px 0px;
	color: #000080;
	font-size: 1.6em;
}

h2 {
	padding: 2px 4px;
	color: #0000FF;
	font-size: 1.4em;
}

h3 {
	padding: 2px 8px;
	color: #0080C0;
	font-size: 1.2em;
}

h4 {
	padding: 2px 12px;
	color: #0080FF;
}

a {
	font-size: 1em;
	display: inline-block;
	padding: 0px 3px;
}

a:link {
	color: blue;
	text-decoration: none;
}

a:visited {
	color: #800040;
	text-decoration: none;
}

a:active {
	color: green;
	text-decoration: underline;
}

a:hover,a:focus {
	color: red;
	text-decoration: underline;
	background-color: #B0C4DE;
}

form {
}

/* no border */
.checkbox,
.radio,
.img,
img,
radio,
input.checkbox,
input.radio {
	border: none;
}

input[type="text"],
input[type="password"],
textarea,select {
	padding: 0px, 5px;
	border:1px solid blue;
}

input:focus,
textarea:focus {
	background: #d2d9f0;
}

input[type="text"],
input[type="password"]{
	padding: 0px 6px;
}

dl {
	margin: 10px;
}

-dl -dt {
	display: inline;
	float: left;
	padding-right: 3px;
	text-align: right;
	vertical-align: middle;
	width: 400px;
	height: 20px;
	color: green;
	font-weight: bold;
	margin-right: 10px;
}

-dl -dd {
	-webkit-margin-start: 40px;
	height: 20px;
	display: block;
}

dl dt:after {
	content: ':';
}

ul {
	margin-left: 20px;
}

li {
	margin: 2px 4px;
}

label {
	font-weight: bold;
	color: green;
	white-space: nowrap;
	margin-right: 3px;
}

table{
	margin: 5px;
	border-spacing: 0px;
	border-collapse: collapse;
	border-top: 1px solid silver;
	border-left: 1px solid silver;
}

th,td {
	border-right: 1px solid silver;
	border-bottom: 1px solid silver;
	padding: 5px;
}

th {
	vertical-align: middle;
	text-align: center;
	color: white;
	background-color: #1569C7;
}

td {
	text-align: right;	
}

tr:hover {
	/*  background: lime;*/
}

p {
	line-height: 1.3em;
}

