th:first-child {
	border-radius: 5px 0 0 0;
}

th:last-child {
	border-radius: 0 5px 0 0;
}

tr:last-child td:first-child {
	border-radius: 0 0 0 5px;
}

tr:last-child td:last-child {
	border-radius: 0 0 0 5px;
}

button,
.button,
td.actions a,
.buttons a,
.mbt-beaneditor-submit,
input[type="button"],
input[type="submit"],
#submit{
	border-radius: 6px;
}
/*radius*/
.radius,
.content,
.reply-content,
.simple-content,
.bar-cool,
.panel,
.options,
.right-menu,
.error,
.notice,
.info,
.alert,
.doc,
.page_nav_menu,
table,
input[type="text"],input[type="password"],textarea,select
{
	border-radius: 6px;
}
#main-menu,#main-nav,#menu-orgs {
	border-radius: 10px;
}
#main-content{
	border-radius: 20px;
}
.mbt-error,
.mbt-contain,
.mbt-grid,
.mbt-msg
{
	border-radius: 10px;
}
