/*
 * nav.less
 */

.nav {
	margin-top: 10px;
}

.nav ul {
	list-style: none;
	margin: 0;
	padding: 0px;
}

.nav ul li {
	margin:0px;
	padding: 0px;
	float: left;
	position: relative;
}

.nav ul li:hover,.nav ul li.hover {
	z-index: 100;
}

.nav ul li a {
	margin:0px;
	padding:0px;
	padding: 2px 5px;
	color: white;
	text-decoration: none;
	display: block;
	border: 0px solid #f1f1f1;
	position: relative;
	margin-left: -1px;
}

.nav ul li a:hover,
.nav ul li a:focus,
.nav ul li a:active,
.nav ul li.hover a{
	color:blue;
	background: #eee;
}

.nav ul ul {
	margin: 0;
	padding: 0;
	border: 1px solid #eee;
	position: absolute;
	top: 100%;
	left: -9999em;
	width: 180px;
}

.nav ul li:hover ul,
.nav ul li.hover ul {
	left: 0;
}

.nav ul ul li {
	margin:0px;
	padding:0px;
	border:0px;
	float: none;
	position: static;
}

.nav ul li:hover ul a,
.nav ul li.hover ul a {
	margin:0px;
	padding-left: 5px;
	color: blue;
	background: #f1f1f1;
	border: 0px solid #eee;
	margin-top: 0px;
}

.nav ul li:hover ul a:hover,
.nav ul li.hover ul a:hover,
.nav ul li.hover ul a:focus,
.nav ul li.hover ul a:active{
	color:white;
	background: blue;
}
